import React, { Component } from "react";
import { Route, Redirect, Switch, NavLink, withRouter } from "react-router-dom";
import PubSub from "pubsub-js";
import Search from "../pages/Search";
import Trending from "../pages/Trending";
import Selection from "../pages/Selection";
import ProjectTraining from "../pages/ProjectTraining";
import UserRank from "../pages/UserRank";
import Top from "../pages/Top";
import Blog from "../pages/Blog";
import Star from "../pages/Star";
import "./BasicLayout.scss";
import { BackTop } from "antd";
import Logo from "../assets/icon/github.png";
import Menu from "../assets/icon/menu.svg";
import Search1 from "../assets/icon/search.png";
const menuList = [
  { label: "趋势", imgClass: "qushi", path: "/trend" },
  { label: "精选", imgClass: "star", path: "/selection" },
  { label: "排行榜", imgClass: "rank", path: "/top" },
  { label: "索引", imgClass: "suoyin", path: "/star" },
  { label: "实战项目", imgClass: "xiangmu", path: "/project_training" },
  { label: "搜索", imgClass: "sousuo", path: "/search" },
  { label: "开发者榜单", imgClass: "paihangbang", path: "/user_rank" },
  { label: "技术博客", imgClass: "bokeyuan", path: "/blog" },
];
class BasicLayout extends Component {
  state = { isExpand: false };
  handleJump = (item) => {
    return (e) => {
      this.props.history.push(item.path);
    };
  };
  handleSearch = (e) => {
    if (e.keyCode === 13) {
      PubSub.publish("search", e.target.value);
      this.props.history.push("/search");
    }
  };
  render() {
    const { isExpand } = this.state;
    const { pathname } = this.props.history.location;
    return (
      <div className="basicLayout">
        <header>
          <div className="logo_wrap">
            <img
              src={Menu}
              alt="logo"
              className="menu"
              onClick={() => {
                this.setState({ isExpand: true });
              }}
            />
            <img src={Logo} alt="logo" className="logo" />
            <span>GitHub中文社区</span>
          </div>
          <div
            className="searchContent"
            style={{ display: pathname === "/search" ? "flex" : "none" }}
          >
            <div className="search_wrap">
              <input
                type="text"
                placeholder="项目搜索..."
                onKeyUp={this.handleSearch}
              />
              <img src={Search1} alt="search" />
            </div>
          </div>
        </header>
        <main>
          <nav className={isExpand ? "expand" : ""}>
            {menuList.map((item, index) => {
              return (
                <div className="menu_list" key={index}>
                  <NavLink to={item.path} activeClassName="active">
                    <div className="menu_content">
                      {/* <img src={item.img} alt={item.label} /> */}
                      <i className={`iconfont icon-${item.imgClass}`}></i>
                      <span>{item.label}</span>
                    </div>
                  </NavLink>
                </div>
              );
            })}
          </nav>
          <div className="show_content">
              <BackTop/>
              <Switch>
                <Route path="/search" component={Search}></Route>
                <Route path="/trend" component={Trending}></Route>
                <Route path="/star" component={Star}></Route>
                <Route path="/top" component={Top}></Route>
                <Route path="/selection" component={Selection}></Route>
                <Route
                  path="/project_training"
                  component={ProjectTraining}
                ></Route>
                <Route path="/user_rank" component={UserRank}></Route>
                <Route path="/blog" component={Blog}></Route>
                <Redirect to="/search"></Redirect>
              </Switch>
          </div>
        </main>
        <footer>©copy right</footer>
        <div
          className="mask"
          style={{ display: isExpand ? "block" : "none" }}
          onClick={() => {
            this.setState({ isExpand: false });
          }}
        ></div>
      </div>
    );
  }
}

export default withRouter(BasicLayout);
