import React, { Component } from 'react';
import './index.scss'
class Href extends Component {
  jumpTo = (url) => {
    window.open(url)
  }
  render() {
    const { children, href } = this.props
    return (
      <span className="href" onClick={ ()=>{ this.jumpTo(href) } }>
        { children }
      </span>
    );
  }
}

export default Href;