import React, { PureComponent } from "react";
import "./index.scss";
import China from "../../components/China";
import { projectList } from "./projectList.js";
export default class index extends PureComponent {
  render() {
    return (
      <div className="content_wrap">
        <div className="markdown-body">
          <div>
            <h2>GitHub实战项目</h2>
            <p>
              精心整理GitHub上从<strong>入门级</strong>到已用于
              <strong>生产坏境</strong>的完整项目。
              从优秀的开源项目中我们可以学习到例如：程序架构设计、设计模式，数据库设计，设计理念、性能优化等众多先进理念。
              收藏本页，长期更新~。
            </p>
            <blockquote>
              有<China margin="0 4px"></China>
              图标，表示国人发起项目或有中文翻译
            </blockquote>
          </div>
          {projectList.map((el) => {
            return (
              <div key={el.title}>
                <h4>{el.title}</h4>
                <ul>
                  {el.data.map((item, index) => {
                    return (
                      <li key={ index }>
                        {item.isChina ? <China margin="0 4px 0 0"></China> : ""}
                        <a href={`https://github.com/${item.link}`}>
                          {item.link}
                        </a>
                        {" " + item.text}
                      </li>
                    );
                  })}
                </ul>
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}
