import React, { Component } from 'react';
import Dialog from '../Dialog'
import Href from '../Href'
import Tag from '../Tag'
import Loading from '../Loading'
import axios from 'axios'
import './index.scss'
import { colorMap } from '../../utils/map.js'
import { getStar } from '../../utils/number.js'
class ResBox extends Component {
  // 初始化state
  state = { visible: false, chooseItem: { owner: {} }, tagObj: { java: 1, javaScript: 2 } }
  // 打开dialog
  openDialog = (item) => {
    axios.get(item.languages_url).then(res => {
      this.setState({ tagObj: res.data })
    })
    this.setState({ visible: true, chooseItem: item })
  }
  // 关闭dialog
  closeDialog = () => {
    this.setState({ visible: false })
  }
  // 点击跳转
  jumpTo = (url) => {
    return (e) => {
      e.stopPropagation()
      window.open(url)
    }
  }
  render() {
    const { resData: { items: resList }, loading, ifRank } = this.props
    const { visible, chooseItem, tagObj } = this.state
    return (
      <div className="res_box">
        <Loading visible={loading} />
        {ifRank ? <h3 className="h3_title">Top 10</h3> : ''}
        {
          resList.map((item, index) => {
            const resItem = (
              <div className="res_item" key={ item.id ? item.id : item._id } onClick={() => { this.openDialog(item) }}>
                    <div className="res_wrap">
                      <div className="res_header">
                        <div className="res_avatar">
                          <img src={item.owner.avatar_url} alt={item.name} />
                        </div>
                        <div className="title_wrap">
                          <div className="title" onClick={this.jumpTo(item.html_url)}>
                            {item.name}
                          </div>
                          <div className="description" onClick={this.jumpTo(item.owner.html_url)}>
                            {item.owner.login}
                            {/* @{item.full_name} */}
                          </div>
                        </div>
                      </div>
                      <div className="res_content">
                        {item.description}
                      </div>
                    </div>
                    <div className="res_footer">
                      <span className="span" style={{ background: colorMap[item.language] ? colorMap[item.language] : "#3572A5" }} className="point"></span>
                      {item.language ? item.language : 'Jupyter Notebook'}
                      <span className="span" style={{ marginBottom: '2px' }}>
                        <i className="iconfont icon-star"></i>
                        {getStar(item.stargazers_count)}
                      </span>

                    </div>
                  </div>
            )
            if ((index === 10 || (index - 10) % 50 === 0) && ifRank) {
              return (
                <React.Fragment key={ item.id ? item.id : item._id }>
                  <h3 className="h3_title">{`Top ${index + 1} - ${index + 50}`}</h3>
                  {resItem}
                </React.Fragment>
              )
            }
            else {
              return resItem
            }
          })
        }
        <Dialog
          dialogVisible={visible}
          headerVisible={false}
          footerVisible={false}
          close={this.closeDialog}
          width='90%'
          borderColor={colorMap[chooseItem.language] ? colorMap[chooseItem.language] : "#3572A5"}
        >
          <div className="res_box">
            <div className="res_item" style={{ flexBasis: ' 99%', maxWidth: '99%', border: 'none', boxShadow: 'none' }}>
              <div className="res_wrap">
                <div className="res_header" style={{ paddingBottom: '0px' }}>
                  <div className="res_avatar">
                    <img src={chooseItem.owner.avatar_url} alt={chooseItem.name} />
                  </div>
                  <div className="title_wrap">
                    <div className="title">
                      {chooseItem.name}
                    </div>
                    <div className="description">
                      {chooseItem.owner.login} @{chooseItem.full_name}
                    </div>
                  </div>
                </div>
                <div className="res_footer" style={{ padding: '16px 8px' }}>
                  <span className="span" style={{ background: colorMap[chooseItem.language] ? colorMap[chooseItem.language] : "#3572A5" }} className="point"></span>
                  {chooseItem.language ? chooseItem.language : 'Jupyter Notebook'}
                  <span className="span" style={{ marginBottom: '2px' }}>
                    <i className="iconfont icon-star"></i>
                    {getStar(chooseItem.stargazers_count)}
                  </span>
                  <span className="span">
                    <i className="iconfont icon-bx-git-repo-forked"></i>
                    {getStar(chooseItem.forks_count)}
                  </span>
                  <span className="span" style={{ display: chooseItem.homepage ? 'block' : 'none' }}>
                    <i className="iconfont icon-net"></i>
                    <Href href={chooseItem.homepage}> {chooseItem.homepage}</Href>
                  </span>
                </div>
                <div className="res_content" style={{ color: ' black', 'fontSize': '15px' }}>
                  {chooseItem.description}
                </div>
                <div className="res_tagList">
                  {
                    Object.keys(tagObj).map((item, index) => {
                      return (
                        <Tag handleClick={this.jumpTo(`https://github.com/topics/${item}`)} type="primary" key={item + index}>{item}</Tag>
                      )
                    })
                  }
                </div>
              </div>
            </div>
          </div>
        </Dialog>
      </div>
    );
  }
}

export default ResBox;