import React, { Component } from "react";
import axios from "../../utils/request";
import Loading2 from "../../components/Loading2";
import ResBox from "../../components/ResBox";
import { languageList } from "./languageList.js";
import { Select } from "antd";
import "./index.scss";
class Top extends Component {
  state = {
    resData: { items: [], total_count: 0 },
    loading: true,
    loading2: false,
    language: "all",
  };
  handleSearch = (q = "stars:>0") => {
    // let keyword = this.input.value https://api.github.com/search/users?q=${keyword}
    const {
      resData: { items: arr },
      language,
    } = this.state;
    let page;
    const pageSize = 20;
    if (arr.length === 0) {
      page = 1;
    } else {
      page = arr.length / pageSize + 1;
      this.setState({ loading2: true });
    }
    axios({
      method: "get",
      url: `http://localhost:8081/repositories`,
      params: { page, pageSize, language },
    })
      .then((res) => {
        this.setState({
          resData: {
            items: [...arr, ...res.data.items],
            total_count: res.data.total_count,
          },
          loading: false,
        });
        arr.length === 0
          ? this.setState({ loading: false })
          : this.setState({ loading2: false });
      })
      .catch((err) => {
        arr.length === 0
          ? this.setState({ loading: false })
          : this.setState({ loading2: false });
      });
  };

  handleChange = (value) => {
    this.setState(
      { resData: { items: [], total_count: 0 }, language: value },
      () => {
        this.handleSearch();
      }
    );
  };
  componentDidMount() {
    this.handleSearch();
    document
      .getElementsByClassName("show_content")[0]
      .addEventListener("scroll", this.scollCallback);
  }
  componentWillUnmount() {
    document
      .getElementsByClassName("show_content")[0]
      .removeEventListener("scroll", this.scollCallback);
  }
  scollCallback = () => {
    let p = document.getElementsByClassName("show_content")[0];
    if (p.scrollHeight === p.scrollTop + p.clientHeight) {
      this.handleSearch();
    }
  };
  render() {
    const { resData, loading, loading2, language } = this.state;
    const { Option } = Select;

    return (
      <div className="content_wrap">
        <div id="top_body">
          <div className="resBox_title">
            github开源项目排行榜
            <Select
              defaultValue={language}
              className="select"
              onChange={ this.handleChange }
            >
              {languageList.map((item) => {
                return (
                  <Option key={item.value} value={item.value}>
                    {item.label}
                  </Option>
                );
              })}
            </Select>
          </div>

          <ResBox resData={resData} loading={loading} ifRank={true}></ResBox>

          <Loading2 visible={loading2}></Loading2>
        </div>
      </div>
    );
  }
}

export default Top;
