import React, { Component } from "react";
import { Table } from 'antd'
import axios from "axios";
import Loading2 from '../../components/Loading2'
import { Switch } from 'antd';
import { Columns } from './tableColumns.js'
import './index.scss'
export default class UserRank extends Component {
  state = { userList: [], showUserList: [], loading2: false };
  getUser = ( pageSize) => {
    const { userList } = this.state;
    let page
    if(userList.length === 0){
      page = 1
    }
    else{
      page = userList.length / pageSize + 1
      this.setState({loading2: true})
    }
    axios({
      method: "get",
      url: `http://localhost:8081/users`,
      params: { page, pageSize },
    })
      .then((res) => {
        const { items } = res.data;
        this.setState({ userList: [...userList, ...items], loading2: false });
        this.setState({ showUserList: [...userList, ...items], loading2: false });
      })
      .catch((err) => {
        console.log(err);
      });
  };
  scollCallback = () =>{
    let p = document.getElementsByClassName('show_content')[0]
    if (p.scrollHeight === p.scrollTop + p.clientHeight) {
      this.getUser(20)
    }
  }
  onChange = (checked) => {
    console.log(`switch to ${checked}`);
    const { userList } = this.state
    checked ? this.setState({ showUserList: userList.filter(el =>  el['location'] && el['location'].indexOf('China') > -1 ), loading2: false }) : this.setState({ showUserList: userList, loading2: false });
  }
  componentDidMount() {
    this.getUser(1000);
    // document.getElementsByClassName('show_content')[0].addEventListener("scroll", this.scollCallback)
  }
  // componentWillUnmount(){
  //   document.getElementsByClassName('show_content')[0].removeEventListener("scroll", this.scollCallback)
  // }
  render() {
    const { showUserList, loading2 } = this.state;
    return (
      <div className="content_wrap">
        <div className="user_title">
        <h2>GitHub开发者排行榜</h2>
        <div><Switch  onChange={this.onChange} /> 中国</div>
        </div>
        <Table columns={ Columns } dataSource={ showUserList } bordered={ true }></Table>
        <Loading2 visible={loading2}></Loading2>
      </div>
    );
  }
}
