import React, { Component } from 'react';
import './index.scss'
class Tag extends Component {
  render() {
    const { type, children, handleClick } = this.props 
    return (
      <span className="tag" onClick={ handleClick } style={{ color : type === 'warning' ? 'rgb(218, 91, 11)' : '#0366d6'}}>
        { children }
      </span>
    );
  }
}

Tag.defaultProps = {
  handleClick:()=>{},
  type:'primary'

}

export default Tag;