import React, { Component } from "react";
import axios from "axios";
import Loading2 from "../../components/Loading2";
import "./index.scss";
import { subscriptionList } from "./subscriptionList.js";
export default class Blog extends Component {
  state = { blogList: [], loading2: false };

  getBlog = (pageSize) => {
    const { blogList } = this.state;
    let page;
    if (blogList.length === 0) {
      page = 1;
    } else {
      page = blogList.length / pageSize + 1;
      this.setState({ loading2: true });
    }
    axios({
      method: "get",
      url: `http://localhost:8081/blogs`,
      params: { page, pageSize },
    })
      .then((res) => {
        const { items } = res.data;
        console.log(items);
        this.setState({ blogList: [...blogList, ...items], loading2: false });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  handleOpenTo = (link) => {
    return (event)=>{
      event.preventDefault();
      window.open(link);
    }
    
  };

  scollCallback = () => {
    let p = document.getElementsByClassName("show_content")[0];
    if (p.scrollHeight === p.scrollTop + p.clientHeight) {
      this.getBlog(20);
    }
  };

  componentDidMount() {
    this.getBlog(20);
    document
      .getElementsByClassName("show_content")[0]
      .addEventListener("scroll", this.scollCallback);
  }

  componentWillUnmount() {
    document
      .getElementsByClassName("show_content")[0]
      .removeEventListener("scroll", this.scollCallback);
  }
  
  render() {
    const { blogList, loading2 } = this.state;
    return (
      <div className="blog_main">
        <div className="subscription_container">
          <p>订阅</p>
          {subscriptionList.map((item, index) => {
            return (
              <React.Fragment key={index}>
                <div className="position_title">{item.title}</div>
                <div className="subscription_content">
                  {item.content.map((item2, index2) => {
                    return (
                      <div className="subscription_item" key={index2}>
                        <div className="company_content">
                          <img
                            className="company_avatar"
                            src={item2.avatar_url}
                            alt={item2.label}
                          />
                          <span>{item2.label}</span>
                        </div>
                        <div className="item_content">
                          {item2.content.map((item3, index3) => {
                            return (
                              <div key={index3}>
                                <a href={item3.html_url} onClick={this.handleOpenTo(item3.html_url)}>{item3.label}</a>
                              </div>
                            );
                          })}
                        </div>
                      </div>
                    );
                  })}
                </div>
              </React.Fragment>
            );
          })}
        </div>
        <div className="blog_container">
          <h2>国内技术团队博客</h2>
          {blogList.map((item, index) => {
            return (
              <div className="blog_item" key={index}>
                <a
                  className="title"
                  href={item.link}
                  onClick={this.handleOpenTo(item.link)}
                >
                  {item.title}
                </a>
                <div className="site">
                  <span>{`@${item.siteName}`}</span>
                  <span>{`@${new Date(item.postTime)
                    .toLocaleDateString()
                    .replaceAll("/", "-")}`}</span>
                </div>
                <p className="description">{item.description}</p>
              </div>
            );
          })}
          <Loading2 visible={loading2} />
        </div>
      </div>
    );
  }
}
