import React, { PureComponent } from "react";
import "./index.scss";
import China from "../../components/China";
import { selectionList } from "./selectionList.js";
export default class index extends PureComponent {
  render() {
    return (
      <div className="content_wrap">
        <div className="markdown-body">
          <div>
            <h2>GitHub精选</h2>
            <p>
              精心整理GitHub上优质开源项目与资源，帮助开发者快速检索开源项目，以及提升知识技能。长期更新~
            </p>
            <blockquote>
              有<China margin="0 4px"></China>
              图标，表示国人发起项目或有中文翻译
            </blockquote>
          </div>
          {selectionList.map((el) => {
            return (
              <div key={el.title}>
                <h4>{el.title}</h4>
                <ul>
                  {el.data.map((item) => {
                    return (
                      <li key={item.link}>
                        {item.isChina ? <China margin="0 4px 0 0"></China> : ""}
                        <a href={`https://github.com/${item.link}`}>
                          {item.link}
                        </a>
                        {" " + item.text}
                      </li>
                    );
                  })}
                </ul>
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}
