import React, { Component } from 'react';
import PubSub from 'pubsub-js'
import axios from '../../utils/request'
import ResBox from '../../components/ResBox'
class Search extends Component {
  state = {resData:{items:[],total_count:0}, loading: true }
  handleSearch = (q='stars:>0') => {
    // let keyword = this.input.value https://api.github.com/search/users?q=${keyword}
    const {resData:{items:arr}} = this.state
    this.setState({
      loading:true,
      resData:{items:arr.slice(0,7),total_count:0}
    })
    axios.get(
      `https://api.github.com/search/repositories?q=${q}&page=1&per_page=100 `,
      {headers: { Authorization: "token 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"}}
    )
    .then(res => {
     this.setState({
       resData:res.data,
       loading:false
     })
    }).catch(err => {
      this.setState({
        loading:false
      })
    })
  }

  componentDidMount(){
    this.handleSearch()
    this.subscribe = PubSub.subscribe('search', (_, val) => {
      this.handleSearch(val)
    })
  }
  componentWillUnmount(){
    PubSub.unsubscribe(this.subscribe)
  }
  render() {
    const { resData, loading } = this.state
    return (
      <div className="content_wrap">
        <ResBox resData={ resData } loading={ loading }>

        </ResBox>
      </div>
    );
  }
}

export default Search;